import React, { useState, useEffect } from "react";
import './Customer.scss';
import AccordionItem from './Accordion';
import axios from 'axios';
import { API_URL } from '../../config/contansts';

const Customer = () => {
  const [categories, setCategories] = useState([]); // FAQ  
  const [selectedCategory, setSelectedCategory] = useState({}); //  FAQ 
  const [searchT, setSearchT] = useState(''); // 

  useEffect(() => { // FAQ    useEffect
    axios.get(`${API_URL}/api/faq`)
      .then(response => {
        //console.log("response:",response);
        setCategories(response.data); // FAQ  
        setSelectedCategory({ category: '' }); //    
      })
      .catch(error => console.error(':', error));
  }, []); //       

  const handleCategory = (category) => { // FAQ     
    setSelectedCategory(category);
  };

  const handleSearch = (e) => { //     
    setSearchT(e.target.value);
  };
  //     
  const uniqueCategories = Array.from(new Set(categories.map(category => category.category)));

  return (
    <div className="customer-container">
      <h2 className="center"></h2>
      <h1 className="center">  </h1>
      <div className="search-container">
        {/* FAQ    */}
        <div className="dropdown">
          {/* select     */}
          <select className="dropmenu"
            //     
            value={selectedCategory.category}
            //      
            onChange={(e) => handleCategory({ category: e.target.value })}
          >
            {/*      map   */}
            {[...uniqueCategories].map(categoryName => (
              //     ,      
              <option key={categoryName} value={categoryName}>{categoryName}</option>
            ))}
          </select>
        </div>

        <input
          className="custom-input"
          type="text"
          value={searchT}
          onChange={handleSearch}
          placeholder=""
        />
      </div>
      {/* FAQ     */}
      <div className="category-buttons">
        {/* uniqueCategories  map      */}
        {uniqueCategories.map(categoryName => (
          <button
            //      
            key={categoryName}
            //      
            onClick={() => handleCategory({ category: categoryName })}
            //       ,  active
            className={selectedCategory.category === categoryName ? 'active' : ''}
          >
            {/*     */}
            {categoryName}
          </button>
        ))}
      </div>

      {/* FAQ       */}
      <div className="accordion-container">
        {selectedCategory.category && selectedCategory.category !== '' ? (
          <div>
            {/*      */}
            {categories
              .filter(category => category.category === selectedCategory.category)
              // (searchT)  
              //       
              .filter(item =>
                item.name.toLowerCase().includes(searchT.toLowerCase()) ||
                item.description.toLowerCase().includes(searchT.toLowerCase())
              )
              .map((item, index) => (
                //     AccordionItem  
                <AccordionItem key={index} category={item} item={item} />
              ))}
          </div>
        ) : (
          <div>
            {/*     */}
            {categories
              .filter(category => category.category !== '')
              .filter(item =>
                item.name.toLowerCase().includes(searchT.toLowerCase()) ||
                item.description.toLowerCase().includes(searchT.toLowerCase())
              )
              .map((item, index) => (
                //     AccordionItem  
                <AccordionItem key={index} category={item} item={item} />
              ))}
          </div>
        )}
      </div>

    </div>
  );
}

export default Customer;

