import React from "react";
import { NavLink } from "react-router-dom";
import { API_URL } from "../../../config/contansts";
import './Exp.scss';

const Experience = () => {
  const temp = {
    exp_imagg1_h1: "",
    exp_22_h3: "    ,\n    .",
    exp_22_strong: "The Brightest Moment\nof Your Life",
    exp_22_p: "     ",
    exp_3_1_h2: "Golden Apfel Story",
    exp_3_1_p: "    \n    ,            \n       A to Z    .",
    exp_3_3_1_h3: "   ",
    exp_3_3_1_p: "       \n       .\n       \n        \n      .",
    exp_3_3_2_h3: "   ",
    exp_3_3_2_p: "       .\n ,  & ,   \n,  , ,     \n        .<br></br>     \n      .",
  }
  return(
    <>
      <div class="exp-1">
        <div class="exp-imagg">
          <div class="exp-imagg1">
            <h3>EXPERIENCE</h3>
            <h1>{temp.exp_imagg1_h1}</h1>
          </div>
         </div>
             <div class="exp-2">
                <ul>
                   <li><NavLink to="/exp"> </NavLink></li>
                   <li><NavLink to="/exp1"> </NavLink></li>
                   <li><NavLink to="/exp2"> </NavLink></li>
                   <li><NavLink to="/exp3"> </NavLink></li>
                 </ul>
             </div>
        <div>
          <div class="exp-22">
            <h3>{temp.exp_22_h3}</h3>
            <strong>{temp.exp_22_strong }</strong>
            <p>{temp.exp_22_p}</p>
            </div>
        </div>
        <div class="exp-3">
          <div class="exp-3-1">
          <h2>{temp.exp_3_1_h2}</h2>
          <p>{temp.exp_3_1_p}
            </p>
          </div>
          <div class="exp-3-imagg2"></div>
          <div class="exp-3-2">
            <h2>Signature Experience</h2>
          </div>
          <div class="exp-3-3">
            <ul>
              <li>
                <img src={API_URL +"/image/Exp/Exp/rose-petals-3194062_640.jpg"}></img>
                <div>
                  <h3>{temp.exp_3_3_1_h3}</h3>
                  <p>{temp.exp_3_3_1_p}</p>
                </div>
              </li>
              <li>
                <img src={API_URL +"/image/Exp/Exp/pink-wine-1964457_640.jpg"}></img>
                <div>
                  <h3>{temp.exp_3_3_2_h3}</h3>
                  <p>{temp.exp_3_3_2_p}</p>
                </div>
              </li>
            </ul>
          </div>
          <div class="exp-bod"></div>
        </div>
          
      </div>
      
    </>
  )
}

export default Experience;