// Date4 
import React, { useEffect, useState } from 'react';
import LeftDate from './leftDate';
import { useRecoilState } from "recoil";
import { startDateState, timeState, plannerState, pointState, contactChoiceState, themeState, plannerIdState } from "../../../recoil/atoms/loginState";
import axios from 'axios';
import { API_URL } from '../../../config/contansts';

const Date4 = () => {
  const [planner, setPlanner] = useRecoilState(plannerState); //useState   
  const [plannerId, setPlannerId] = useRecoilState(plannerIdState); //useState   
  const [point, setpoint] = useRecoilState(pointState); //useState   
  const [date, setDate] = useRecoilState(startDateState); //useState   
  const [time, setTime] = useRecoilState(timeState); //useState   
  const [contactChoice, setContactChoice] = useRecoilState(contactChoiceState); //useState   
  const [theme, setTheme] = useRecoilState(themeState); //useState   
  const [planners, setPlanners] = useState([]);

  useEffect(()=>{
    axios.get(`${API_URL}/api/planner?point=${point}`)
      .then(res => {
        setPlanners(res.data)
        console.log(res.data);
      }).catch(e => {
        console.log("Planners.js/e: ",e);
      })
  },[])

  const handleReservationClick = () => {
    axios.post(`${API_URL}/api/reservation`, {plannerId, point, date, time, contactChoice, theme})
      .then(res => {
        console.log(res);
        alert(" ");
      }).catch(e => {
        console.log("Planners.js/handleReservationClick()/e: ",e);
      })
  }

  const handlePlannerChange = (e) => {
    console.log(e.target.dataset.id);
    setPlanner(e.target.dataset.name);
    setPlannerId(e.target.dataset.id);
    // setPlanner({
    //   ...formData,
    //   [e.target.name]: e.target.value,
    // });
  };

  // const planners =[
  //   {profileImgUrl: "/img/main4/4.png", name:"", phone:"000-0000-0000", introduction:"   5  "},
  //   {profileImgUrl: "/img/main4/4.png", name:"", phone:"000-0000-0000", introduction:"   12  "},
  //   {profileImgUrl: "/img/main4/4.png", name:"", phone:"000-0000-0000", introduction:"   51   "}
  // ]

  return (
    <div>
      <div className='Date-container'>
        <div className='Date-content'>
          <div className='Date-warp'>
            <div className='Date-warp2'>
              <div className='Date-tit'>
                <LeftDate />
              </div>
              <div className='Date-tit2'>
                <div className='Date-tit3'>
                  <h3>  .</h3>
                </div>
                <ul>
                  <div className="day-of-week-selector-container22">
                    {planners.map(planner => (
                      <div key={planner.name} className="days-of-week">
                          <img src={API_URL + planner.profileImgUrl} alt={''} />
                          <div className='proname'>
                            <h1><span>{planner.name}</span></h1>
                            <h2><span>{planner.phone}</span></h2>
                            <div className='introduction'>
                              <h3>{planner.introduction}</h3>
                            </div>
                            <button data-id={planner.id} data-name={planner.name} onClick={handlePlannerChange}></button>
                          </div>
                      </div>
                    ))}
                  </div>
                </ul>
                <button onClick={handleReservationClick}> </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Date4;
