import React, { useState, useEffect } from "react";
import './Products.scss';
import MenuBar from "./Menubar";
import { NavLink, useParams } from "react-router-dom";
import axios from 'axios';
import { API_URL } from '../../config/contansts';

const Product = () => {
  const { id } = useParams();
  const Data = {
    'prodpho': { name: '', photo: '/image/Product/main/photo.jpg' },
    'prodmov': { name: '', photo: '/image/Product/main/movie.jpg' },
    'prodhair': { name: '&', photo: '/image/Product/main/hair.jpg' },
    'prodbooth': { name: '', photo: '/image/Product/main/booth.jpg' },
    'prodetc': { name: '/', photo: '/image/Product/main/Etc.jpg' },
    'prodcar': { name: '', photo: '/image/Product/main/car.jpg' },
    'prodbow': { name: '', photo: '/image/Product/main/bow.jpg' },
    'prodwdmg': { name: '', photo: '/image/Product/main/WdMg.jpg' },
  };

  const data = Data[id];
  const [productsData, setProductsData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(`${API_URL}/api/products/${id}`);
        console.log("Product.js/response: ",response);
        setProductsData(response.data);
      } catch (error) {
        console.error('    :', error);
      }
    };

    

    fetchData();
  }, [id]);

  const backgroundImageStyle = {
    backgroundImage: `url(/app3${data ? data.photo : ''})`,
  };

  // const filteredProducts = productsData.filter(product => product.category === id);

  return (
    <>
      <div className="product-page1" style={backgroundImageStyle}>
        <div className="product-content">
          <h1>Product</h1>
          <h3>{data ? data.name : ' .'}</h3>
        </div>
      </div>
      <MenuBar categories={['prodpho', 'prodmov', 'prodhair', 'prodbooth', 'prodetc', 'prodcar', 'prodbow', 'prodwdmg']} />
      <div className="photo-cards">
        {productsData.map(product => (
          <NavLink to={`/product/${id}/${product.id}`} key={product.id}>
            <div className="photo-card">
              <img src={API_URL + product.imageUrl} alt={""} />
              <div className="card-details">
                <h3>
                  <span>{product.title} </span>
                  {product.name}
                </h3>
                <p>{`${(+product.price).toLocaleString()}`}</p>
              </div>
            </div>
          </NavLink>
        ))}
      </div>
    </>
  );
}

export default Product;
