import React from 'react';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import './Join-input.scss'
import { API_URL } from '../../config/contansts'
const Joininput = () => {
    const navigate = useNavigate();

    const onsubmitJoin = async (e) => {
        e.preventDefault();
        const id = e.target.id.value
        const pwd = e.target.pwd.value
        const confirmPwd = e.target.confirmPwd.value
        const name = e.target.name.value
        const gender = e.target.gender.value
        const birth = e.target.birth.value
        const phone = e.target.phone.value
        const companionName = e.target.companionName.value
        const companionPhone = e.target.companionPhone.value

        // pwd === confirmPwd && id !== "" && pwd !== "" && confirmPwd !== "" && name !== "" && phone !== "" && gender !== ""&& birth !==""
        // && companionPhone !==""&&companionName!==""

        if(true){
            console.log("g c c : ",gender, companionName, companionPhone);
            await axios.post(`${API_URL}/api/user/join`,{name, id, pwd, phone, birth, gender, companionName, companionPhone})
            .then(() =>{
                alert("!");
                navigate('/');  
            })
            .catch(err =>{
                console.error(err);
            })
        }else{
            return alert(" ");
        }
    };

    return (
<form id="join-input-form" onSubmit={onsubmitJoin} >
    <h3>MEMBER</h3>
    <h1></h1>

        <ul id='join-input'>
            <li>
                <div className='input-box'>
                    <h5> *</h5><input id='name' type="text" placeholder=''/>
                </div>
                <div>
                    <h5> *</h5><input id='id' type="text" placeholder=',8~16 '/>
                </div>
            </li>
            <li>
                <div className='input-box'>
                    <h5> *</h5><input id='pwd' type="password" placeholder=' '/>
                </div>
                <div>
                    <h5>  *</h5><input id='confirmPwd' type="password" placeholder=' '/>
                </div>
            </li>
            <li>
                <div className='input-box'>
                    <h5> *</h5><input id='phone' type="text" placeholder=' '/>
                </div>
                <div className='input-box'>
                    <h5>  *</h5><input id='birth' type="date" />
                    <div>
                        <input type="radio" className='gender' id='gender' name="gender" value="man"/>
                        <input type="radio" className='gender' id='gender' name="gender" value="woman"/>
                    </div>
                </div>
            </li>
            <li>
                <div className='input-box'>
                    <h5>  *</h5><input id='companionName' type="text" />
                </div>
                <div className='input-box'>
                    <h5>  *</h5><input id='companionPhone' type="text" />
                </div>
            </li>
            <li>
                <button type="submit" id='join-btn'></button>
            </li>

        </ul>

</form>
    );
}
//1
export default Joininput;