import React, { useState } from 'react';
import './Mypage3.scss';
import MyPageTop from '../../components/MyPage/MyPageTop';

const Mypage3 = () => {
  const [selectedButton, setSelectedButton] = useState('button1');

  const handleButtonClick = (button) => {
    setSelectedButton(button);
  };

  return (
    <div className="Mypage-container">
      <div className='my-backgrund'>
        <MyPageTop/>
      </div>
      <div className='my-backgrund2'>
        <div className={`invitation-container ${selectedButton === 'button1' ? 'style1' : selectedButton === 'button2' ? 'style2' : 'style3'}`}>
          <h1> </h1>
          <p>    .</p>
          <div className="details">
            <p className="date">: 2023 12 1</p>
            <p>:  3</p>
            <p>:  </p>
            <p>:    123-456</p>
          </div>
        </div>
        <div className="buttons">
            <button onClick={() => handleButtonClick('button1')}></button>
            <button onClick={() => handleButtonClick('button2')}></button>
            <button onClick={() => handleButtonClick('button3')}></button>
          </div>
      </div>
    </div>
  );
};


//1
export default Mypage3;
