import React, { useState } from 'react';
import '../Mypage/Mypage.scss';
import LeftBar from '../../components/MyPage/LeftBar';
import MyPageTop from '../../components/MyPage/MyPageTop';
import { TextField, Button, Container, Box } from '@mui/material';
import axios from 'axios';
import { API_URL } from '../../config/contansts';
import useAsync from '../../hooks/useAsync';
import { useNavigate } from 'react-router-dom';
import { useRecoilState } from "recoil";
import { loginState } from "../../recoil/atoms/loginState";

function MyPage() {
  console.log(' ');
  const navigate = useNavigate();
  const [isLogin, setIsLogin] = useRecoilState(loginState); //useState   
  const [selectedMenuItem, setSelectedMenuItem] = useState(' ');
  const [formData, setFormData] = useState({
    id: '',
    password: '',
    confirmPassword: '',
    email: '',
  });
  const getUser = async () =>{
    console.log("Mypage.js/getUser()");
    const user = await axios.get(`${API_URL}/api/user/mypage`);
    console.log("Mypage.js/getUser(): ", user.status);
    return user.data;
  }
  
  const [state] = useAsync(getUser, []);
  const { loading, data:profile, error} = state; //state 
  if(loading) return <div> ......</div>
  if(error) { 
    alert('  ');
    setIsLogin(false);
    navigate('/login');
    return <div> .</div> 
  }
  if(!profile){
    return <div>.</div>
  }  
  // console.log(profile);
  const name = profile.name;
  const id = profile.id;
  const pwd = profile.pwd;
  const birth = profile.birth;
  const phone = profile.phone;
  const companionName = profile.companionName;
  const companionPhone = profile.companionPhone;

  const handleMenuClick = (menuItem) => {
    setSelectedMenuItem(menuItem);
  };

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleUpdateProfile = () => {
    //      .
    // formData    .
    console.log('Updated Profile:', formData);
  };

  const user = { name: '', spouse: '', age: '25', phone: '000-0000-0000', bh: '1999-10-08' }
  const pay = { name: '', prodname:'A', price: '1000', how:'', map: '', memo: ' ' }
  const cons = { name: '', consname:'', day: '2023-11-16', prodname:'A', memo: '  ' }
  

  return (
    <div className="Mypage-container">
      <div className="my-backgrund">
        <MyPageTop name={name}/>
      </div>
      <div className="sub-menu">
        <div className="my-both">
          <div className="left-list">
            <LeftBar onMenuClick={handleMenuClick} />
          </div>
          <div className="my-content">
            <h1>{selectedMenuItem}</h1>
            <div className="my-content-main">
              {selectedMenuItem === ' ' && (
                <>
                  <h2>: {name}</h2>
                  <h2>: {birth}</h2>
                  <h2>: {phone}</h2>
                  <h2>: {companionName}</h2>
                  <h2>: {companionPhone}</h2>
                </>
              )}
              {selectedMenuItem === ' ' && (
                <form>
                  <Container maxWidth="xs">
                    <Box mt={3}>
                    </Box>
                    <Box mt={2}>
                      <TextField fullWidth label="" variant="outlined" required name="id" onChange={handleInputChange} />
                    </Box>
                    <Box mt={2}>
                      <TextField fullWidth label="" variant="outlined" required type="password" name="password" onChange={handleInputChange} />
                    </Box>
                    <Box mt={2}>
                      <TextField fullWidth label=" " variant="outlined" required type="password" name="confirmPassword" onChange={handleInputChange} />
                    </Box>
                    <Box mt={2}>
                      <TextField fullWidth label="" variant="outlined" required type="email" name="email" onChange={handleInputChange} />
                    </Box>
                    <Box mt={2} mb={2}>
                      <Button fullWidth variant="contained" color="primary"  type="button" onClick={handleUpdateProfile}>
                        
                      </Button>
                    </Box>
                  </Container>
                </form>
              )}
              {selectedMenuItem === ' ' && (
                <>
                  <h2> : {pay.name}</h2>
                  <h2>  : {pay.prodname}</h2>
                  <h2> : {pay.price}</h2>
                  <h2>  : {pay.map}</h2>
                  <h2>  (, ,   ) : {pay.how} </h2>
                  <h2> : {pay.memo}</h2>
                </>
              )}
              {selectedMenuItem === '  ' && (
                <>
                  <h2> : {cons.name}</h2>
                  <h2>  : {cons.consname}</h2>
                  <h2>  : {cons.day}</h2>
                  <h2>  : {cons.prodname}</h2>
                  <h2> : {cons.memo}</h2>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MyPage;
