import React, { useState } from "react";
import "../Header/Header.scss";
import { NavLink, useNavigate } from "react-router-dom";
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import axios from "axios";
import { API_URL } from "../../config/contansts";
import { useRecoilState } from "recoil";
import { loginState } from "../../recoil/atoms/loginState";

const Header = () => {
  const navigate = useNavigate();
  const [isLogin, setIsLogin] = useRecoilState(loginState); //useState   
  // const login = getCookie('accessToken');
  console.log("login: ", isLogin);

  const logout = async () => {
    axios.get(`${API_URL}/api/logout`, { withCredentials: true })
      .then(()=>{
        setIsLogin(false);
      })
      .catch((err) => {
        console.log("logout/err: ", err);
      })
  } 

  
  const productData = [
    { id: 'prodpho', name: '  ', category: '  ' },
    { id: 'prodpho', name: ' ', category: ' ' },
    { id: 'prodmov', name: ' ', category: ' ' },
    { id: 'prodhair', name: '&', category: '&' },
    { id: 'prodbooth', name: '', category: '' },
    { id: 'prodetc', name: '/', category: '/' },
    { id: 'prodcar', name: '', category: '' },
    { id: 'prodbow', name: '', category: '' },
    { id: 'prodwdmg', name: '', category: '' },
  ];

    const [isHeaderHovered, setIsHeaderHovered] = useState(false);

  return (
    <div id="header-container"
      className={isHeaderHovered ? 'hovered' : ''}
      onMouseEnter={() => setIsHeaderHovered(true)}
      onMouseLeave={() => setIsHeaderHovered(false)}>
      <div className='header-warp'>
        <NavLink to='/'><img src={API_URL +'/img/header/logo.png'} alt=''></img></NavLink>
        <div className='header-item'>
          <ul>
            <li>
              <NavLink to='/sangdam2'>RESERVATIONS</NavLink>
              <ul id='sub-menu'>
                {/* <li><NavLink to='/reservations'></NavLink></li> */}
                <li><NavLink to='/sangdam2'></NavLink></li>
              </ul>
            </li>
            <li>
              <NavLink to='/wedding/1'>WEDDING</NavLink>
              <ul id='sub-menu'>
                <li><NavLink to='/wedding/1'></NavLink></li>
                <li><NavLink to='/wedding/2'></NavLink></li>
                <li><NavLink to='/wedding/3'></NavLink></li>
                <li><NavLink to='/wedding/4'></NavLink></li>
                <li><NavLink to='/wedding/5'></NavLink></li>
              </ul>
            </li>
            <li>
              <NavLink to='product/prodpho'>PRODUCTS</NavLink>
              <ul id='sub-menu'>
                {productData.map((item) => (
                  <li key={item.id}>
                    <NavLink to={`product/${item.id}`}>
                      {item.name}
                    </NavLink>
                  </li>
                ))}
              </ul>
            </li>
            <li>
              <NavLink to='/party'>PARTY</NavLink>
              <ul id='sub-menu'>
                <li><NavLink to='/party'></NavLink></li>
              </ul>
            </li>
            <li>
              <NavLink to='/exp'>EXPERIENCE</NavLink>
              <ul id='sub-menu'>
                <li><NavLink to='/exp'></NavLink></li>
                <li><NavLink to='/exp1'> </NavLink></li>
                <li><NavLink to='/exp2'> </NavLink></li>
                <li><NavLink to='/exp3'> </NavLink></li>
              </ul>
            </li>
            <li>
              <NavLink to='/about'>ABOUT</NavLink>
              <ul id='sub-menu'>
                <li><NavLink to='/about'> </NavLink></li>
                <li><NavLink to='/about1'></NavLink></li>
              </ul>
            </li>
            <li>
              <NavLink to='/faq'>CUSTOMER</NavLink>
              <ul id='sub-menu'>
                <li><NavLink to='/faq'>FAQ</NavLink></li>
              </ul>
            </li>
            {isLogin ? 
              <li>
                <NavLink to='/mypage'><PersonOutlineIcon fontSize='medium'/></NavLink>
                <ul id='sub-menu'>
                  <li><NavLink to="/mypage"></NavLink></li>
                  <li><NavLink to="/" onClick={logout}></NavLink></li>
                </ul>
              </li>
              :
              <li>
                <NavLink to='/'><PersonOutlineIcon fontSize='medium'/></NavLink>
                <ul id='sub-menu'>
                  <li><NavLink to="/login"></NavLink></li>
                  <li><NavLink to="/join"> </NavLink></li>
                </ul>
              </li>
            }
            
          </ul>
        </div>
      </div>
    </div>
  );
}
export default Header;

