import { useReducer, useEffect } from "react"
const initialState = {
    loading: false,
    data: null,
    error: null
}
// ?   ?   
// LOADING , SUCCESS, ERROR
function reducer(state, action){
    switch(action.type) {
        case "LOADING":
            return {
                loading: true,
                data: null,
                error: null
            };
        case "SUCCESS":
            return {
                loading: false,
                data: action.data,
                error: null
            }
        case "ERROR":
            return {
                loading: false,
                data: null,
                error: action.error
            }
        default:
            return state;
    }
}

function useAsync(callback, deps = []) {
    // console.log("useAsync: ", deps);
    const [state, dispatch] = useReducer(reducer, initialState);
    const fetchDate = async () => {
        dispatch({type: "LOADING"});
        try {
            const data = await callback(); //callback getProduct()
            // console.log("data: ", data);
            dispatch({
                type: "SUCCESS",
                data: data
            })
            if(data==false){
                dispatch({
                    type: "ERROR",
                    error: ""
                })  
            }
        }
        catch(e){
            dispatch({
                type: "ERROR",
                error: e
            })
        }
    }
    useEffect(()=>{ // useEffect(()=>{}, [])  1  
        fetchDate();
    // eslint-disable-next-line
    }, deps); //deps ([]) 34
    return [state, fetchDate];
}
export default useAsync