// LeftDate 
import React from 'react';
import { useRecoilState } from "recoil";
import { startDateState, timeState, plannerState, pointState, contactChoiceState, themeState } from "../../../recoil/atoms/loginState";

const LeftDate = () => {
  const [startDate, setStartDate] = useRecoilState(startDateState); //useState   
  const [time, setTime] = useRecoilState(timeState); //useState   
  const [planner, setPlanner] = useRecoilState(plannerState); //useState   
  const [point, setPoint] = useRecoilState(pointState); //useState   
  const [contactChoice, setcontactChoice] = useRecoilState(contactChoiceState); //useState   
  const [theme, settheme] = useRecoilState(themeState); //useState   


  const user = {
    brand: 'WeddingRoom',
    point: point,
    contactChoice:contactChoice,
    theme:theme,
    start: startDate,
    time: time, //   .
    month: planner, //   .
  };

  return (
    <div className="left-Date">
      <dl>
        <dt></dt>
        <dd>
          <span>{user.brand}</span>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <span>{user.point}</span>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <span>{user.start}</span>
        </dd>
      </dl>
      <dl>
        <dt> </dt>
        <dd>
          <span>{user.time}</span>
        </dd>
      </dl>
      <dl>
        <dt>  </dt>
        <dd>
          <span>{user.contactChoice}</span>
        </dd>
      </dl>
      <dl>
        <dt> </dt>
        <dd>
          <span>{user.theme}</span>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <span>{user.month}</span>
        </dd>
      </dl>
    </div>
  );
};

export default LeftDate;
